@import url(https://fonts.googleapis.com/css2?family=Fondamento&Roboto:wght@300;500;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{background-color:gray;width:100%;min-height:100vh;overflow:hidden;font-family:'Fondamento', cursive}.app main{width:80%;margin:auto;height:100%;color:white}.app main h1{font-size:2em;letter-spacing:2px}.app main .data_box{margin-top:20px}.app main .date{letter-spacing:1px}.app main .coordinates span{padding:0 10px;font-family:'Roboto', sans-serif}.app main .coordinates span strong{letter-spacing:2px;font-family:'Fondamento', cursive}.app main .search-box{width:100%}.app main .search-box .search-bar{width:60%;display:block;margin:10px auto;padding:10px;outline:none;background:rgba(255,255,255,0.815);border:0;font-family:'Fondamento', cursive;border-radius:2px;font-size:1.5em}.app main .search-box .search-bar:focus{background:rgba(255,255,255,0.918)}.app main .location-box .location{font-size:4.2em;color:white}.app main .weather-box .indicator-Icon{display:flex;justify-content:center;align-items:center}.app main .weather-box .indicator-Icon .svgIcon{width:100px;height:100px}.app main .weather-box .weather strong{letter-spacing:2px;text-transform:capitalize;font-size:2em}.app main .weather-box .temp h1{font-size:4em;display:inline;padding-right:5px}.app main .weather-box .temp span{font-size:3.1em}.app main .weather-box .small_Panel{margin-top:40px;display:flex;justify-content:space-evenly;align-items:center}.app main .weather-box .small_Panel .indicator-Icon .svgIcon{width:50px;height:50px}.app main .weather-box .small_Panel span strong{letter-spacing:1px;font-family:'Roboto', sans-serif}.bg{width:100%;height:100vh;background-position:center;background-size:cover;background-repeat:no-repeat;text-align:center}.svgIcon{fill:white}@media only screen and (max-width: 768px){.app main .search-box .search-bar{font-size:1em !important}.app main .location-box .location{font-size:4em}}@media only screen and (max-width: 513px){.app main h1{font-size:1.3em;margin:5px !important;margin-top:20px !important}.app main .search-box .search-bar{width:80% !important}.app main .location-box .location{font-size:2.5em}.coordinates span{padding:0 10px;font-family:'Roboto', sans-serif;font-size:0.8em !important}.coordinates span strong{letter-spacing:2px;font-family:'Fondamento', cursive}}

